.main-page {
    user-select: none;
    padding-top: 60px;
    padding-bottom: 60px;
    overflow-y: auto;
    width: 99%;
    overflow-x: hidden;

}

.main-header {
    display: flex;
    max-width: 100%;
    margin-left: auto;
    margin-bottom: 24px;
}

.profile {
    display: flex;
    justify-content: center;
    width: 40px;
    max-width: 40px;
    height: 40px;
    cursor: pointer;
    margin-right: 20px;
    margin-left: 12px;
    background: #C9C9C9;
    border: 2.5px solid #EEEEEE;
    border-radius: 50px;
    
}
.profile >svg{
    position: relative;
    top: 13px;
    clip-path:circle(55% at 50% 45%)
}
nav {
    display: flex;
    margin-bottom: 20px;
}

.user-name{

font-family: 'Pretendard';
font-style: normal;
margin-top: 15px;
font-weight: 700;
font-size: 18px;
line-height: 16px;
text-align: center;

color: #333333;


}

nav{
    margin:auto;

    justify-content: center;
    gap: 20px;
    margin-bottom: 40px;
}

.today-sentence-wrapper{
    
font-family: 'Pretendard';
display: flex;
flex-direction: column;
align-items: center;
justify-content: center;
padding: 12px;

max-width: 326px;
height: 270px;
margin-bottom: 40px;
margin-left: auto;
margin-right: auto;
background: var(--gray05);
border-radius: 8px;

}

.today-sentence{
    
font-family: 'Pretendard';
font-weight: 700;
font-size: 20px;
max-width: 156px;
line-height: 28px;
text-align: center;
margin-bottom: 48px;
color: #1C1C1C;

}
.today-sentence-wrapper > h2{

max-width: 156px;
height: 22px;

font-family: 'Pretendard Variable';
font-weight: 700;
font-size: 18px;
line-height: 22px;
text-align: center;

color: #1C1C1C;


}

.today-sentence-wrapper > h1{

max-width: 156px;
height: 56px;

font-family: 'Pretendard Variable';
font-weight: 700;
font-size: 20px;
line-height: 28px;
text-align: center;
color: #1C1C1C;

flex: none;
order: 1;
flex-grow: 0;

}
.studied-wrapper {

width: 85%;
max-width: 134.5px;
height: 80px;

background: #F7F0FE;
border-radius: 8px;

font-family: 'Pretendard';
font-style: normal;
font-weight: 600;
font-size: 16px;
padding: 16px;
text-align: left;

color: #5F5F5F;
vertical-align: middle;

}


.studied-number{
    font-family: 'Pretendard';
    font-style: normal;
    font-weight: 700;
    font-size: 20px;
    position: relative;
    top: 40px;
    line-height: 16px;
    text-align: right;
    color: #913FF7;
    
    }

    .studied-number>svg{
        margin-right: 8px;
        position: relative;
        top: 3px;
    }


.explore-videos {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.video-category{
display: flex;
flex-direction: column;
align-items: flex-start;
padding: 0px;
gap: 2px;


margin: 10px;
font-family: 'Pretendard Variable';
font-style: normal;
font-weight: 700;
font-size: 18px;
line-height: 22px;
display: flex;

color: #1C1C1C;


}

@media screen {
    
}
.explore-video-list {
    display: flex;
    justify-content: flex-start;
    max-width: 100%;
    overflow-x: scroll;
}

.explore-video-list::-webkit-scrollbar {
    height: 10px;
  }
  .explore-video-list::-webkit-scrollbar-track {
    background: #e8e8e8;
  }
  .explore-video-list::-webkit-scrollbar-thumb {
    background: #ccc;
    border-radius: 4px;
  }
  .explore-video-list::-webkit-scrollbar-thumb:hover {
    background: #bbb;
  }
  
.explore-video-content {
    background-color: var(--gray10);
    margin-bottom: 10px;
    cursor: pointer;
    max-width: 250px;
    border-radius: 20px;
}


.explore-video-title {
font-family: 'Pretendard';
font-style: normal;
font-weight: 600;
font-size: 16px;
max-width: 250px;
display: flex;
align-items: center;

color: #5F5F5F;

}

.bottom-navbar {

    width: 100%;
    
    position: fixed;
    bottom: 0;
    left: 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.bottom-navbar-btn {
gap: 7px;
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;
cursor:pointer;

height: 64px;

background: #FFFFFF;
font-family: 'Pretendard';
font-style: normal;
font-weight: 600;
font-size: 12px;
text-align: center;

color: var(--gray30);
border:none;
background-color: white;


}

form{
    width: 100%;
    padding: 10px;
}

.form-wrapper{
display: flex;
flex-direction: column;
justify-content: center;
align-items: center;

max-width: 347px;
height: 124px;

background: var(--gray05);
border-radius: 8px;
margin-left: auto;
margin-right: auto;
margin-top: 50px;
margin-bottom: 10px;
}
.form-label{

max-width: 325px;
height: 16px;
text-align: center;

font-family: 'Pretendard';
font-style: normal;
font-weight: 700;
font-size: 14px;
text-align: center;
margin-bottom: 20px;

/* Gray/70 */
color: #5F5F5F;

}

.form-input{
border:none;
width: 75%;
max-width: 213px;
margin-left: 2%;
height: 36px;

background: #FFFFFF;
border-radius: 8px;
text-align: center;
}

.form-submit{
gap: 12px;

border:none;
width: 30%;
max-width: 70px;
margin-left: 16px;
height: 44px;
background: #913FF7;
border-radius: 8px;

color: white;
}


.category-container {
    width: 100%;

    margin-bottom: 64px;
    
}

.video-category {
font-weight: 700;
font-size: 18px;
color: #1C1C1C;


    margin-bottom: 16px;
    width: 100%;
    margin-left: 0;
}

.explore-video {
    width: 100%;
    margin-right: 10px;
    margin-bottom: 20px;
    
}

